.app_header {
  width: 100vw;

  min-height: 15vh;
  display: flex;
  flex-direction: colu mn;
  /* position: sticky; */
  top: 1px;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  color: white;
  background-color: #253e30;
}
  .app_header ul {
    max-width: 30vw;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-self: flex-end;
  }
  .app_header ul .menu_item {
      height: 100%;
      display: flex;
      border-bottom: 2px solid #253e30;
      list-style: none;
    }
  .app_header ul .menu_item a {
        color: #fff;
        text-decoration: none;
      }
  .app_header ul .menu_item:hover {
        transition: 0.5s ease all;
        border-bottom: 2px solid #1f6f8c;
      }

@media (max-width: 768px) {
  .app_header {
    min-height: 7vh;
  }
}

@media (max-width: 1024px) {
  .app_header {
    min-height: 7vh;
    font-size: calc(8px + 2vmin);
  }

    .app_header ul {
      max-width: 90vw;
      width: 90vw;
      margin: 10px;
      /* align-self: flex-end; */
      justify-content: space-between;
    }
}
